"use client";

import { useRouter, usePathname } from "next/navigation";
import { LayoutGrid, User, BookOpen, CreditCard, LogOut } from "lucide-react";
import { useAuthContext } from "@/app/context/AuthContext";

export default function Sidebar() {
  const router = useRouter();
  const pathname = usePathname();
  const { broker, logout } = useAuthContext();

  const menu = [
    { label: "Overview", href: "/dashboard", icon: LayoutGrid },
    { label: "Leads", href: "/dashboard/leads", icon: BookOpen },
    { label: "Packages", href: "/dashboard/packages", icon: CreditCard },
    { label: "Profile", href: "/dashboard/profile", icon: User },
    { label: "Logout", href: "/logout", icon: LogOut, action: logout },
  ];

  return (
    <aside className="w-64 fixed top-20 left-6 h-[calc(100vh-8rem)] bg-gradient-to-b from-blue-600 to-blue-700 shadow-2xl rounded-2xl p-6 flex flex-col text-white">
      {/* Broker Info Card */}
      <div className="mb-8 pb-6 border-b border-blue-400/30">
        <div className="w-16 h-16 bg-white/20 backdrop-blur-sm rounded-full mx-auto mb-3 flex items-center justify-center">
          <User className="w-8 h-8 text-white" />
        </div>
        <h2 className="font-bold text-lg text-center text-white">
          {broker?.name || "Broker"}
        </h2>
        <p className="text-sm text-blue-100 text-center mt-1">
          {broker?.phoneNumber}
        </p>
      </div>

      {/* Navigation Menu */}
      <nav className="flex-1 overflow-y-auto">
        <ul className="space-y-2">
          {menu.map((item) => {
            const isActive = pathname === item.href;
            const isLogout = item.label === "Logout";
            
            return (
              <li key={item.label}>
                <button
                  onClick={() => item.action ? item.action() : router.push(item.href)}
                  className={`w-full flex items-center gap-3 px-4 py-3 rounded-xl transition-all duration-200 ${
                    isActive
                      ? "bg-white text-blue-600 shadow-lg font-semibold"
                      : isLogout
                      ? "hover:bg-red-500/20 text-blue-50"
                      : "hover:bg-white/10 text-blue-50"
                  }`}
                >
                  <item.icon className={`w-5 h-5 ${isActive ? "text-blue-600" : ""}`} />
                  <span className="text-[15px]">{item.label}</span>
                </button>
              </li>
            );
          })}
        </ul>
      </nav>

      {/* Footer Badge */}
      <div className="mt-6 pt-4 border-t border-blue-400/30">
        <div className="text-center">
          <p className="text-xs text-blue-200">Powered by</p>
          <p className="text-sm font-semibold text-white">Rentify</p>
        </div>
      </div>
    </aside>
  );
}
